body {
  height: 100vh;
  margin: 0;
  display: grid;
  place-items: center;
}
#base {
  display: flex;
  gap: 100px;
}
#main {
  height: 250px;
  padding: 30px;
  padding-bottom: 30px;
  border: 1px solid #BDBDBD;
  border-radius: 8px;
  background: linear-gradient( to bottom, white 70%, #d6e8F6 0% );
  box-shadow: 0px 5px 20px 5px #cccccc;
}
#main2 {
  padding: 30px;
  padding-bottom: 50px;
  border: 1px solid #BDBDBD;
  border-radius: 8px;
  background: linear-gradient( to bottom, #eeffe8 20%, white 80% );
  box-shadow: 0px 5px 20px 5px #cccccc;
}
#inputBox {
  width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}
#select {
  width: 90px;
  height: 30px;
  text-align: center;
  margin-right: 10px;
}
#ratioInput {
  height: 25px;
  width: 50px;
  text-align: center;
}
#resultInput {
  height: 25px;
  width: 95px;
  text-align: center;
}
#label {
  font-size: 8pt;
  padding-left: 5px;
}
#warning {
  font-size: 8pt;
  padding-left: 5px;
  text-align: center;
  color: red;
/*   visibility: hidden; */
}
#resultBox {
  width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.center {
  text-align: center;
}